.advantages__item {
    // width: 270px;
    margin: 0 auto;
    list-style-type: disc;
    // display: inline-block;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 23px;
    /* or 177% */


    /* text */

    color: #FFFFFF;

    opacity: 0.8;
    text-align: start;
   
        @media screen and (min-width: 768px) {
                width: 509px;
  
                font-size: 13px;
            }

    @media screen and (min-width: 1024px) {
        font-size: 11px;
        line-height: 22px;
        // justify-self: center;
    width: 71.14%;
