}

.instagram-svg {
    fill: #FFFFFF;
    opacity: 0.8;
}


.advantages__item:not(:last-child) {
    margin-bottom: 10px;
}


 
.advantages__list {
    margin-top: 30px;
        padding-left: 4%;
            @media screen and (min-width: 768px) {
            
                    padding-left: 0%;
                }

    @media screen and (min-width: 1024px) {
        
    display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 0px;
        align-items: center;
        justify-items: center;
        // grid-auto-rows: minm ax(100px, auto);
        grid-gap: 0px;
            grid-template-columns: repeat(2, 1fr);
            place-items: SELF-END;
            display: grid;
        }

}

.advantages__position {
    @media screen and (min-width: 768px) {
            position: relative;
        }
}
